.filter__button {
  border: 1px solid $brown;
  width: 100%;
  background-color: $wight;
  padding: 25px 20px;
  margin-bottom: 25px;
  cursor: pointer;

  &:hover,
  &:focus {
    background-color: $brown;
    color: $wight;
  }
}

.filter__open {
  display: none;

  @media (max-width: $desktop-width) {
    display: block;
  }
}

.filter__modal {
  @media (max-width: $desktop-width) {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: $grey;
    z-index: 200;
    overflow-y: scroll;
    overflow-x: hidden;
    display: none;

    &--passive {
      display: none;
    }

    &--active {
      display: block;
    }
  }
}

.filter__form {
  @media (max-width: $desktop-width) {
    width: 440px;

    &--closed {
      display: none;
    }

    &--opened {
      position: absolute;
      background-color: $wight;
      padding: 20px 200px 40px 20px;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }
  }

  @media (max-width: $tablet-width) {
    width: 280px;
    padding: 20px 20px;
  }
}

.filter__close {
  display: none;

  @media (max-width: $desktop-width) {
    display: block;
    width: 32px;
    height: 32px;
    position: absolute;
    background-color: $black-opopacity-01;
    cursor: pointer;
    right: 20px;
    top: 20px;
    border-radius: 50%;

    &::after,
    &::before {
      content: "";
      position: absolute;
      background-color: $wight;
      width: 14px;
      height: 2px;
      top: 11px;
      right: 6px;
    }

    &::after {
      transform: rotate(45deg);
    }

    &::before {
      transform: rotate(-45deg);
    }

    &:hover,
    &:focus {
      opacity: 0.5;
    }
  }
}

.filter__wrap {
  border: 1px solid $brown;
  padding-top: 24px;
  padding-bottom: 25px;
  padding-left: 24px;

  &--range {
    padding-top: 15px;
    margin-bottom: 32px;
  }
}

.filter__name {
  border: none;
  padding: 0;
  margin: 0;
}

.filter__legend {
  width: 100%;
  position: relative;
  cursor: pointer;

  &::after {
    content: "";
    position: absolute;
    background-image: url("../img/svg/pointer-down.svg");
    background-repeat: no-repeat;
    width: 15px;
    height: 15px;
    top: 5px;
    right: 20px;
  }

  &--active {
    &::after {
      transform-origin: center center;
      transform: rotate(180deg);
    }
  }

  &:hover,
  &:focus {
    opacity: 0.5;
  }
}

.filter__list {
  list-style: none;
  padding: 0;
  margin: 0;
  margin-top: 23px;
  padding-left: 10px;
  font-size: 14px;
  line-height: 18px;
}

.filter__legend--close + .filter__list {
  display: none;
}

.filter__checkbox {
  margin-bottom: 12px;

  &:last-child {
    margin-bottom: 0;
  }
}

.filter-checkbox-input {
  display: none;
}

.filter-checkbox-check {
  display: inline-block;
  vertical-align: middle;
  width: 16px;
  height: 15px;
  border: 1px solid $brown;
  margin-right: 10px;
  cursor: pointer;
}

.filter-checkbox-input:checked + .filter-checkbox-check {
  background-color: $brown;
}

.filter__controls {
  position: relative;
  padding: 26px 20px 23px 23px;
}

.filter__scale {
  height: 1px;
  background-color: $grey;
}

.filter__bar {
  position: absolute;
  right: 40px;
  height: 1px;
  background-color: $brown;
  padding-left: 30px;
  width: 50%;
}

.filter__toggle {
  width: 4px;
  height: 4px;
  background-color: $brown;
  border: 8px solid $brown;
  border-radius: 50%;
  position: absolute;
  top: 20px;
  left: 45px;
  cursor: pointer;

  &--max {
    left: 150px;
  }
}

.filter__text {
  position: absolute;
  left: -20px;
  top: 10px;
}
